.profileScreen {
    height: 100vh;
    color: white;
}

.profileScreen__body {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    max-width: 80%;
    padding-top: 8%;
}

.profileScreen__body h1 {
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
}

.profileScreen__info {
    display: flex;
}

.profileScreen__info img {
    height: 100px;
}

.profileScreen__details {
    color: white;
    margin-left: 25px;
    flex: 1;
}

.profileScreen__details h2 {
    background-color: grey;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
}

.profileScreen__plans {
    margin-top: 20px;
}

.profileScreen__plans h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;
}

.profileScreen__button {
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: #fff;
    background-color: #e50914;
    font-weight: 600;
    border: none;
    cursor: pointer;
}

@media (max-width: 800px) {

    .profileScreen__body {
        width: 80%;
        padding-top: 15%;
    }

}

@media (max-width: 550px) {

    .profileScreen__body {
        padding-top: 20%;
        margin-left: 30px;
    }

}

@media (max-width: 500px) {

    .profileScreen__body {
        padding-top: 35%;
    }

    .profileScreen__body h1 {
        font-size: 30px;
    }

    .profileScreen__info img {
        height: 50px;
    }

}

@media (max-width: 450px) {

    .profileScreen__details{
        margin-left: 0px;
    }

    .profileScreen__info {
        display: ruby;
    }

    .profileScreen__body h1 {
        text-align: center;
    }

    .profileScreen__info img {
        height: 80px;
        padding-bottom: 15px;
    }

}



